import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { toast } from 'react-toastify'
import Layout from '../components/Layout/Layout'
import {Button, Select} from 'antd'

import {useNavigate} from 'react-router-dom'
import { Container } from 'react-bootstrap'

import addPhoto from '../Assets/add-photo.png'

const {Option} = Select
const Posts = () => {
    const [categories,setCategories] = useState([])
    const [photo,setPhoto] = useState("")
    const [name,setName] = useState("")
    const [category,setCategory] = useState("")

    const navigate = useNavigate()


      //get all categories=================================================
  const getAllCategory = async()=>{
    try {
      const {data} = await axios.get('/api/v1/category/all-category')
      if(data?.success){
        setCategories(data?.category);
      }
    } catch (error) {
      console.log(error)
      toast.error("Error while getting Categories")
      
    }
  }

  useEffect(()=>{
    getAllCategory();
  },[]);

  // ==================================================================


  //create post =======================================================
  const handleUpload = async(e)=>{
    e.preventDefault()
    try {
        const postData = new FormData()
        postData.append("name",name)
        postData.append("category",category)
        postData.append("photo",photo)

        const {data} = axios.post('/api/v1/post/create-post',postData)
        if(data?.success){
            toast.error(data?.message)
        }else{
            toast.success("Uploaded successfully")
            navigate('/post')
           
        }
    } catch (error) {
        console.log(error)
        toast.error("Error in uploading")
        
    }
  }
  // ==================================================================

  return (
    <>
    <Layout>
      <Container>

<div className='mt-5 py-4 px-4'>
<div className='d-flex  justify-content-center'>

<label  className='btn bg-dark m-3 ' >
{
  photo && (
        <img src={URL.createObjectURL(photo)} alt="post" className='img-fluid '  width={"200px"}/>
        )
    }
    {photo ? photo.photo : <img src={addPhoto} alt='add-photo' style={{width:"150px"}}/>}
    <input type="file" name='photo' accept='image/*'  onChange={(e)=>setPhoto(e.target.files[0])} hidden/>
</label>
    </div>
    <Select border={false} placeholder="Select a Category" size="large" showSearch className="form-select mb-3" onChange={(value)=>{setCategory(value)}} >
        {
            categories?.map((c)=>(
              <Option key={c._id} value={c._id}>{c.name}</Option>
              ))
        }
        
    </Select>
   

        <input type="text" value={name} placeholder='Name' className='form-control' onChange={(e)=>setName(e.target.value)} />
   <button className='btn btn-success my-2 ' onClick={handleUpload}>Upload</button>
              </div>
          </Container>
    </Layout>
    </>
  )
}

export default Posts