import React, { useEffect, useState } from 'react'
import Layout from '../components/Layout/Layout'
import axios from 'axios'
import { useParams } from 'react-router-dom'
import './Privew.css'
import { Container, Modal } from 'react-bootstrap'

const Privew = () => {
  const params = useParams()
 const [posts,setPosts] = useState([])
 const [category,setCategory] = useState([])
 const [showModal, setShowModal] = useState({});


 const handleShow = (postId) => {
   setShowModal((prev) => ({ ...prev, [postId]: true }));
 };
 
 const handleClose = (postId) => {
   setShowModal((prev) => ({ ...prev, [postId]: false }));
 };
 

 const getPostByCat = async()=>{
  try {
    const {data} = await axios.get(`/api/v1/post/post-category/${params.slug}`)
    setPosts(data?.posts)
    setCategory(data?.category)
  } catch (error) {
    console.log(error)
    
  }
 }

 useEffect(()=>{
  if(params?.slug) getPostByCat();
  // eslint-disable-next-line
 },[params?.slug])

  return (
    <>
    <Layout>
      <Container className='mt-5 py-4'>
<div className='d-flex align-items-center text-white p-2'>

    <h3>{category?.name}&nbsp;</h3>
    <h3>-&nbsp;{posts?.length}</h3>
</div>

    <div className="gallery" >
            {posts?.map((p) => (
              <div className="pics "  key={p._id}>
          
<div onClick={() => handleShow(p._id)}>

          <img
            src={`/api/v1/post/post-photo/${p._id}`}
            className="img-fluid flex-wrap"
            style={{ width: '250px', height: 'auto' }}
            alt={p.name}
            /> 
            </div>
          <Modal
    className='p-3 d-flex align-items-center'
    show={showModal[p._id] || false}
    onHide={() => handleClose(p._id)}
    aria-labelledby={`post-modal-${p._id}`}
    >
      <Modal.Body  className='p-1 bg-dark' >
        <div key={p._id}>
        <img 
          src={`/api/v1/post/post-photo/${p._id}`}
          alt="" className='img-fluid m-0' 
          />
       <button className="btn modal-btn " style={{position:"absolute",top:"2px",right:"2px"}}  onClick={() => handleClose(p._id)}>
       <i className="fa-solid fa-xmark "></i>
        </button>
        </div>
      </Modal.Body>
    </Modal>
          
        </div>
      ))}

      
            </div>
           
      </Container>
    </Layout>
    </>
  )
}

export default Privew