import React from 'react'

const CategoryForm = ({handleSubmit,value,setValue}) => {
    
  return (
    <>
    <form onSubmit={handleSubmit} className='d-flex m-3 gap-2'>
        <input type="text" className='form-control' placeholder='Type here..' value={value} onChange={(e)=> setValue(e.target.value)} />
        <button type='submit' className='btn btn-outline-danger text-white fw-bold'>Create</button>
    </form>
    </>
  )
}

export default CategoryForm