import React, { useEffect, useRef, useState } from 'react'
import Layout from '../components/Layout/Layout'
import axios from 'axios';
import { toast } from 'react-toastify';
import { Link } from 'react-router-dom';
import Hero from './Hero';
import './HomePage.css'
import { Container, Modal } from 'react-bootstrap';

const HomePage = () => {
  const [posts, setPosts] = useState([]);
  const [categories,setCategories] = useState([])
  const [showModal, setShowModal] = useState({});


  const handleShow = (postId) => {
    setShowModal((prev) => ({ ...prev, [postId]: true }));
  };
  
  const handleClose = (postId) => {
    setShowModal((prev) => ({ ...prev, [postId]: false }));
  };
  


      //get all categories=================================================
      const getAllCategory = async()=>{
        try {
          const {data} = await axios.get('/api/v1/category/all-category')
          if(data?.success){
            setCategories(data?.category);
          }
        } catch (error) {
          console.log(error)
          toast.error("Error while getting Categories")
          
        }
      }
    
      useEffect(()=>{
        getAllCategory();
      },[]);
    
      // ==================================================================


  // get posts by catttt
  const getAllPosts = async () => {
    try {
      const { data } = await axios.get("/api/v1/post/all-post");
      setPosts(data.post);
    } catch (error) {
      console.log(error);
      toast.error("Error in getting posts");
    }
  };
    //lifecycle method
    useEffect(() => {
      getAllPosts();
    }, []);


 // Group posts by category
const postsByCategory = {};
posts.forEach((post) => {
  // Check if post.category exists and is not null
  if (post.category && post.category._id) {
    if (!postsByCategory[post.category._id]) {
      postsByCategory[post.category._id] = [];
    }
    if (postsByCategory[post.category._id].length < 6) {
      postsByCategory[post.category._id].push(post);
    }
  }
});

const [isDragging, setIsDragging] = useState(false);
const [startX, setStartX] = useState(0);
const [startScrollLeft, setStartScrollLeft] = useState(0);
const carouselRefs = useRef({});

const handleArrowButtonClick = (btn, categoryId) => {
  if (carouselRefs.current[categoryId]) {
    carouselRefs.current[categoryId].scrollLeft += btn === 'left' ? -200 : 200;
  }
};

const handleDragStart = (e, categoryId) => {
  setIsDragging(true);
  if (carouselRefs.current[categoryId]) {
    carouselRefs.current[categoryId].classList.add('dragging');
    setStartX(e.pageX);
    setStartScrollLeft(carouselRefs.current[categoryId].scrollLeft);
  }
};

const handleDragging = (e, categoryId) => {
  if (!isDragging) return;
  if (carouselRefs.current[categoryId]) {
    carouselRefs.current[categoryId].scrollLeft = startScrollLeft - (e.pageX - startX);
  }
};

const handleDragStop = (categoryId) => {
  setIsDragging(false);
  if (carouselRefs.current[categoryId]) {
    carouselRefs.current[categoryId].classList.remove('dragging');
  }
};

useEffect(() => {
  document.addEventListener('mousemove', handleDragging);
  document.addEventListener('mouseup', handleDragStop);

  return () => {
    document.removeEventListener('mousemove', handleDragging);
    document.removeEventListener('mouseup', handleDragStop);
  };
}, [isDragging]);

  return (
    <>
      <Layout>
        <Hero />
        <Container className='p-4 text-white'>
          {categories?.map((cat) => (
            <div key={cat._id}>
              <div className='d-flex justify-content-between align-items-center'>
                <h2>{cat.name}</h2>
                <div key={cat._id}>
                  <Link to={`/preview/${cat.slug}`} className='nav-link p-0' >
                    <div className='btn btn-secondary fw-bold  text-white m-0 more-btn border-2 bg-black my-3' >
                      More &#160;
                      <i className="fa-solid fa-circle-right " style={{ color: "white", fontSize: "15px" }}> </i>
                    </div>
                  </Link>
                </div>
              </div>

              <div className='wrapper'>
                <i
                  id={`left_${cat._id}`}
                  onClick={() => handleArrowButtonClick('left', cat._id)}
                  className='fa-solid fa-angle-left text-white '
                ></i>
                <ul
                  className="carousel p-0"
                  ref={(el) => (carouselRefs.current[cat._id] = el)}
                  onMouseDown={(e) => handleDragStart(e, cat._id)}
                  onMouseMove={(e) => handleDragging(e, cat._id)}
                  onMouseUp={() => handleDragStop(cat._id)}
                  onMouseLeave={() => handleDragStop(cat._id)}
                >
                  {postsByCategory[cat._id]?.map((p) => (
  <div key={p._id + "_wrapper"}>
    <li className='card' key={p._id}>
      <div className="img" onClick={() => handleShow(p._id)}>
        <img src={`/api/v1/post/post-photo/${p._id}`} alt={p.name} draggable="false" />
      </div>
    </li>
    <Modal
      className='p-3 d-flex align-items-center'
      show={showModal[p._id] || false}
      onHide={() => handleClose(p._id)}
      aria-labelledby={`post-modal-${p._id}`}
    >
      <Modal.Body className='p-1 bg-dark'>
        <div key={p._id + "_modal"}>
          <img
            src={`/api/v1/post/post-photo/${p._id}`}
            alt=""
            className='img-fluid m-0'
          />
          <button className="btn modal-btn " style={{ position: "absolute", top: "2px", right: "2px" }} onClick={() => handleClose(p._id)}>
            <i className="fa-solid fa-xmark "></i>
          </button>
        </div>
      </Modal.Body>
    </Modal>
  </div>
))}
                </ul>
                <i
                  id={`right_${cat._id}`}
                  onClick={() => handleArrowButtonClick('right', cat._id)}
                  className='fa-solid fa-angle-right text-white'
                ></i>
              </div>
              
            </div>
            
          ))}
          
        </Container>
      </Layout>
    </>
  )
}

export default HomePage