import React, { useEffect, useState } from 'react'
import './Header.css'
import { useNavigate } from 'react-router';
const Header = () => {
  const [longPress, setLongPress] = useState(false);
  const navigate = useNavigate();
  let timer;

  const handleStart = () => {
    timer = setTimeout(() => {
      setLongPress(true);
      navigate('/create');
    }, 5000);
  };

  const handleEnd = () => {
    clearTimeout(timer);
    setLongPress(false);
  };

  useEffect(() => {
    document.addEventListener('mousedown', handleStart);
    document.addEventListener('touchstart', handleStart);

    document.addEventListener('mouseup', handleEnd);
    document.addEventListener('touchend', handleEnd);

    return () => {
      document.removeEventListener('mousedown', handleStart);
      document.removeEventListener('touchstart', handleStart);

      document.removeEventListener('mouseup', handleEnd);
      document.removeEventListener('touchend', handleEnd);
    };
  }, [navigate]);
  
  return (
    <>
  <nav className=" text-white d-flex fixed-top rounded" style={{backgroundColor:"#060606"}}>

    <button className="nav-brand p-3"   
    onTouchStart={handleStart}
    onTouchEnd={handleEnd}
    onMouseDown={handleStart}
    onMouseUp={handleEnd}
        style={{
          backgroundColor:"#060606",width:"130px",
          border: 'none',
          background: 'none',
          cursor: 'pointer',
        }}>M_JOHNZY</button>
    <span className='nav-bar bg-dark mt-2 ' style={{width:"100%",height:"48px",borderRadius:"10px 0 0 0"}}>&nbsp;</span>
  </nav>
    </>
  )
}

export default Header