import React, { useEffect, useState } from 'react'
import Layout from '../components/Layout/Layout'
import axios from 'axios'

import Table from 'react-bootstrap/Table';
import { Button, Container} from 'react-bootstrap';
import { Modal } from 'antd';
import CategoryForm from '../components/form/CategoryForm';
import { toast } from 'react-toastify';

const Category = () => {
  const [categories,setCategories] = useState([])
  const [name,setName] = useState("")

  const [visible,setVisible] = useState(false)
  const [selected,setSelected] = useState(null)
  const [updatedName,setUpdatedName] = useState('')



  //handle submit=====================================================
  const handleSubmit = async(e)=>{
    e.preventDefault()
    try {
      const {data} = await axios.post('/api/v1/category/create-category',{name})
      if(data?.success){
        toast.success(`${name} is created`)
        getAllCategory();
      }else{
        toast.error(data.message)
      }
    } catch (error) {
      console.log(error)
      toast.error("error in category creation ")
      
    }
  }
  // ==================================================================


  //handle update======================================================
  const handleUpdate = async(e)=>{
    e.preventDefault()
    try {
      const {data} = await axios.put(`/api/v1/category/update-category/${selected._id}`,{name:updatedName})
      if(data.success){
        toast.success(`Name is updated to ${updatedName}`)
        setSelected(null)
        setUpdatedName('')
        setVisible(false)
        getAllCategory();
      }else{
        toast.error(data.message)
      }
    } catch (error) {
      toast.error("error in category update")
      
    }
  }

  // ==================================================================


  //delete category====================================================
  const handleDelete = async(pid)=>{
    try {
      const {data} = await axios.delete(`/api/v1/category/delete-category/${pid}`)
      if(data.success){
        toast.success(`Category is Deleted`)
        getAllCategory();
      }else{
        toast.error(data.message)
      }
    } catch (error) {
      toast.error("error in category update")
      
    }
  }

  // ==================================================================


  //get all categories=================================================
  const getAllCategory = async()=>{
    try {
      const {data} = await axios.get('/api/v1/category/all-category')
      if(data?.success){
        setCategories(data?.category);
      }
    } catch (error) {
      console.log(error)
      toast.error("Error while getting Categories")
      
    }
  }

  useEffect(()=>{
    getAllCategory();
  },[]);

  // ==================================================================


  return (
    <>
    <Layout>
<Container className='mt-5 py-4'>

      <div>
        <CategoryForm handleSubmit={handleSubmit} value={name} setValue={setName} />
      </div>
    <Table variant="dark">
      <thead>
        <tr>
          <th>Name</th>
          <th>Actions</th>
          
        </tr>
      </thead>
      <tbody>
        
        {categories?.map((c) => (
          <>
        <tr>
              <td key={c._id}>{c.name}</td>
              <td>

                <Button className="btn btn-primary me-2" onClick={()=>{setVisible(true);setUpdatedName(c.name); setSelected(c)}}>
                <i className='fa-solid fa-edit'></i>
              </Button>
              <Button className="btn btn-danger" onClick={()=>{handleDelete(c._id)}}>
              <i className='fa-solid fa-trash'></i>
                </Button>
                </td>
        </tr>
          </>
              ))}

      </tbody>
    </Table>
    <Modal onCancel={()=>setVisible(false)} footer={null} open={visible}>
      <CategoryForm value={updatedName} setValue={setUpdatedName} handleSubmit={handleUpdate}/>
    </Modal>
</Container>
    </Layout>
    </>

  )
}

export default Category